export class User {

    constructor(
        public username:string, 
        public email:string, 
        public role:string, 
        public id: string, 
        private _jwt: string,
        private _exp: Date
    ) {}

    get token() {
        if (!this._exp || new Date() > this._exp) {
            return null;
        }
        return this._jwt;
    }
}