import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { catchError, tap } from 'rxjs/operators';
import { Subject, throwError } from 'rxjs';
import { User } from './user.model';
import { Router } from '@angular/router';
import { DataStorageService } from '../shared/data-storage.service';
import { RootInjectorGuard } from '../shared/root-injector.guard';

export interface AuthResponseData {
    message: string;
    jwt: string;
    exp: string;
    id: string;
    username: string;
    email: string;
    role: string;
}

@Injectable({providedIn: 'root'})
export class AuthService {

    user = new Subject<User>();
    private tokenExpTime: any;

    constructor(private http: HttpClient, private router: Router) {
    }

    login(user: string, pass: string) {
        return this.http.post<AuthResponseData>(
            "https://parasvag.webpages.auth.gr/NFC/API/authenticate.php",
            {
                username: user,
                password: pass
            },
        ).pipe(catchError(this.handleError), tap(resData => {
            this.handleAuthentication(
                resData.username,
                resData.email,
                resData.role,
                resData.id,
                resData.jwt,
                resData.exp
            )
        }));
    }

    autoLogin(): boolean {
        const userData: {
            username:string, 
            email:string, 
            role:string, 
            id: string, 
            _jwt: string,
            _exp: string
        } = JSON.parse(localStorage.getItem('userData'));

        if (!userData) {
            return false;
        }

        const loadedUser = new User(
            userData.username, 
            userData.email, 
            userData.role, 
            userData.id,
            userData._jwt,
            new Date(userData._exp)
        );
        console.log(loadedUser);
        if (loadedUser.token) {
            this.user.next(loadedUser);
            this.autoLogout(new Date(userData._exp));
            return true;
        }
        else {
            return false;
        }

    }

    logout() {
        this.user.next(null);
        this.router.navigate(["/login"]);
        localStorage.removeItem('userData');
        if (this.tokenExpTime) {
            clearTimeout(this.tokenExpTime);
        }
        this.tokenExpTime = null;
    }

    autoLogout(expirationDate: Date) {
        this.tokenExpTime = setTimeout(() => {
            this.logout();
        }, expirationDate.getTime() - (new Date().getTime()));
    }

    isAuthenticated(): boolean {
        if (!!localStorage.getItem('userData')) return true;
        return false;
    }

    private handleAuthentication(username:string, email:string, role:string, id:string, token:string, exp:string) {
        const expirationDate = new Date(+exp * 1000);
        const user = new User(
            username, 
            email, 
            role, 
            id, 
            token, 
            expirationDate
        );
        this.user.next(user);
        this.autoLogout(expirationDate);
        localStorage.setItem('userData', JSON.stringify(user));
    }

    private handleError(errorRes: HttpErrorResponse) {
        let errorMessage = "An unknown error occured!";
            if (!errorRes.error) {
                return throwError(errorMessage);
            }
            errorMessage = errorRes.error.message;
            return throwError(errorMessage);
    }
}